import {ElMessageBox} from "element-plus";

export async function confirmDialog(message, title = '', type = 'warning') {
    try {
        const confirmResult = await ElMessageBox.confirm(message, title, {
            confirmButtonText: '',
            cancelButtonText: '',
            type: type,
        });
        return confirmResult === 'confirm'; //  true false 
    } catch (error) {
        return false; //  false
    }
}

export function copyToClipboard(text) {
    navigator.clipboard.writeText(text)
        .then(() => {
            this.$message.success('' + text);
        })
        .catch(() => {
            // 
            const textarea = document.createElement('textarea');
            textarea.value = text;
            document.body.appendChild(textarea);
            textarea.select();
            document.execCommand('copy');
            document.body.removeChild(textarea);
            this.$message.success('' + text);
        });
}